import api from '../../server/api';

const state ={
    wraps:[],
    navcontents:[],
    promotions:[],
    hotclasses:[],
    likes:[],
    typeleft:[],
    typerightUpper:[],
    typerightDownNav:[],
    currentFcid:null,
    typerightDownItem:[],
    detailCommits:[],
    keyWord:[],
    allNav:[],
    allProductDatas:[]
}

const getters = {

}

const actions = {
    //主页数据跟分类左侧页面的数据查询
    loadHomeData({commit}){
        api.loadDataByPids({
            appPlat:"m",
            pids:"51822,106930,51833,51836,41789"
        }).then(({data})=>{
            // console.log(data);
            commit("setWraps",data[51822].list);
            commit("setNavcontents",data[106930].list);
            commit("setPromotions",data[51833].list);
            commit("setHotclasses",data[51836].list);
            commit("setTypeleft",data[41789].list);
        }) 
    },
    //猜你喜欢部分的数据查询
    loadLikes({commit}){
        api.loadLikesData({
            pid:"9750",
            sort:"pop",
            cKey:46
        }).then((data)=>{
            // console.log(data);  
            commit("setLikes",data.result.wall.docs);
        })
    },
    //分类页面右侧上面部分的分类数据查询
    loadTypesRightData({commit},pid){
        api.loadTypeRightDataByPid({
            appPlat:"m",
            pid:pid
        }).then((data)=>{
            console.log(data);
            commit("setTyperightUpper",data.data.categoryNavigation.list)

        })
    },
    
    //分类页面右侧下面部分商品上面列表的数据查询
    loadTypeRightDownData({commit},fcid){
        api.loadTypeRightDataByFcid({
            fcid:fcid,
            page:1,
            sort:null,
            cKey:"h5-cube"
        }).then((data)=>{
            state.currentFcid = fcid;
            // console.log(fcid,state.currentFcid);
            // console.log(data);
            // console.log(data.result.sortFilter);
            
            commit("setTyperightDownNav",data.result.sortFilter);
        })
    },
    //分类页面右侧下面部分商品的数据查询
    loadTypeRightDownDataItems({commit},{fcid=state.currentFcid,sort='pop'}){
        api.loadTypeRightDataByFcid({
            fcid:fcid,
            // title:title,
            page:1,
            sort:sort,
            cKey:"16"
        }).then((data)=>{
            // console.log(state.currentFcid,sort); 
            // console.log(data.result.wall.docs);
            commit("setTyperightDownItem",data.result.wall.docs);
        })
    },

    //商品详情页面的评论部分数据查询
    loadDetailCommits({commit},tradeItemId=""){
        api.loadDetailCommit({
            type:1,
            appPlat:"m",
            itemId:tradeItemId
        }).then((data)=>{
            // console.log(data);
            if(data.data){
                commit("setDetailCommits",data.data.list);
                return;
            }
            

        })
    },

    //头顶部分下面关键字数据的查询
    loadKeyWordData({commit}){
        api.loadKeyWordSearch({
            platform:"H5",
            code:"hotWord,sketch"
        }).then((data)=>{
            // console.log(data.data.hotWord.data);
            commit("setKeyWordData",data.data.hotWord.data);

        })
    },

    //头顶搜索框商品数据查询
    loadAllProductDatas({commit},{q="",sort="pop"}){
        api.loadAllProducts({
            q:q,
            cKey:46,
            page:1,
            sort:sort
        }).then((data)=>{
            console.log(q,sort);
            console.log(data.result);
            commit("setAllProductDatas",data.result.wall.docs);
            commit("setAllNav",data.result.sortFilter)

        })
    },
    //从type页面路由过来的搜索框商品数据查询
    loadAllProductDatas2({commit},{fcid=null,sort="pop",title=""}){
        api.loadAllProducts({
            fcid  : fcid,
            title : title,
            cKey  : 16,
            sort  : sort,
            page  : 1
        }).then((data)=>{
            console.log(fcid,title,sort);
            console.log(data);
            commit("setAllProductDatas",data.result.wall.docs);
            commit("setAllNav",data.result.sortFilter)


        })
    }

}

const mutations = {
    setWraps(state,data){
        state.wraps = data;
    },
    setNavcontents(state,data){
        state.navcontents = data;
    },
    setPromotions(state,data){
        state.promotions = data;
    },
    setHotclasses(state,data){
        state.hotclasses = data;
    },
    setLikes(state,data){
        state.likes = data;
    },
    setTypeleft(state,data){
        state.typeleft = data;
    },
    setTyperightUpper(state,data){
        state.typerightUpper = data;
    },
    setTyperightDownNav(state,data){
        state.typerightDownNav = data;
    },
    setTyperightDownItem(state,data){
        state.typerightDownItem = data;
    },
    setCurrentFcid(state,data){
        state.currentFcit = data;
    },
    setDetailCommits(state,data){
        state.detailCommits = data;
    },
    setKeyWordData(state,data){
        state.keyWord = data;
    },
    setAllNav(state,data){
        state.allNav = data;
    },
    setAllProductDatas(state,data){
        state.allProductDatas = data;
    }
}

export default {
    getters,
    mutations,
    actions,
    state
}


