<template>
    <div class="detail">
        <div class="container">
            <BscrollView class="wrapper" ref="wrapper">
                <div class="content">
                    <div class="pageback" @click="$router.go(-1)">
                        <i class="iconfont icon-xiangzuojiantou"></i>
                    </div>
                    <!-- 伪轮播图 -->
                    <div class="detail-pictures">
                         <div class="swiper-wrap">
                            <swiper :options="swiperOptions" ref="mySwiper">
                                <swiper-slide v-for="(v,i) of 3" :key="i">
                                    <img :src="params.img" >
                                </swiper-slide>
                            </swiper>
                        </div>
                    </div>
                    <div class="detail-desc">
                        {{params.title}}
                    </div>
                    <div class="detail-price">
                        <span class="detail-price-cny">￥</span>
                        <span class="detail-price-num">{{ params.price }}</span>
                        <div class="discount-price">
                            <div class="oldprice">￥{{ params.orgPrice }}</div>
                            <div class="discount-text">限量促销</div>
                        </div>
                    </div>

                    <div class="detail-commits">
                        <div class="detail-commit-item" v-for="(v,i) of home.detailCommits" :key="i">
                            <div class="detail-commit-item-user">
                                <img :src="v.user.avatar">
                                <span>{{v.user.uname}}</span>
                            </div>
                            <div class="detail-commit-item-content">
                                <div  class="detail-commit-item-content-text">
                                   {{v.content}}
                                </div>
                                <div  class="detail-commit-item-content-time">
                                   {{v.time}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </BscrollView>
        </div>
        <div class="collect">
            <input type="button" value="加入购物车" @click="addProduct()">
        </div>
    </div>
</template>

<script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    import {mapActions,mapGetters,mapMutations,mapState} from 'vuex';
    export default {
        components:{
            swiper,
            swiperSlide,
        },
        data(){
            return{
                swiperOptions:{
                    autoplay:false, 
                    effect : 'coverflow',
                    slidesPerView: 2,
                    centeredSlides: true,
                    coverflow: {
                    rotate: 30,
                    stretch: 3,
                    depth: 80,
                    modifier: 2,
                    slideShadows : true
                }
                },
                params:{}
                
            }
        },
        computed:{
            ...mapState(['home']),
        },
        methods:{
            ...mapActions(["loadDetailCommits"]),

            addProduct(){
                let user = JSON.parse(sessionStorage.getItem("users"));
                console.log(user);
                let uproduct =localStorage.getItem(user.uname);
                console.log(uproduct);
                if(uproduct==null){
                    console.log(111);
                    uproduct={};
                    this.params.num=1;
                    let key = this.params.tradeItemId;
                    console.log(key);
                    uproduct[key] = this.params;
                    console.log(uproduct);
                    localStorage.setItem(user.uname,JSON.stringify(uproduct));
                    
                }else{
                    uproduct = JSON.parse(uproduct);
                    let key = this.params.tradeItemId;
                    let flag = true;
                    for (const id in uproduct) {
                       if(key == id){
                           uproduct[id].num++;
                           flag =false;
                       }
                    }
                    if(flag){
                        this.params.num = 1;
                        uproduct[key] = this.params;
                    }
                    localStorage.setItem(user.uname,JSON.stringify(uproduct));
                    
                    
                }
               
                
               
               
                
                
                
            }
        },
        mounted(){
            console.log(JSON.parse(sessionStorage.getItem("item")) );
            this.params = JSON.parse(sessionStorage.getItem("item"));
            // this.product.user = JSON.parse(sessionStorage.getItem("users"));

            this.loadDetailCommits(this.params.tradeItemId);
        }
    }
</script>

<style lang="less" scoped>
    @import url("../../global/less");

    .detail{
        position: absolute;
        top:0px;/*px*/
        bottom:100px;/*px*/
        left: 0;
        right: 0;
        .container{
            height:100%;
            .wrapper{
                width:100%;
                height:100%;
                .pageback{
                    position: absolute;
                    top:20px;
                    left:20px;
                    z-index: 999;
                    width:60px;
                    height:60px;
                    border-radius: 30px;
                    background: rgba(51, 51, 51, 0.5);
                    text-align: center;
                    i{
                        font-size:50px;/*px*/
                        color:white;
                    }
                }
                .detail-pictures{
                    width:100%;
                    height:600px;/*px*/
                    img{
                        width:100%;
                        height:auto;
                    } 
                }
                .detail-desc{
                    margin:30px 30px 0px;
                    font-size:32px;/*px*/
                }
                .detail-price{
                    display: inline-block;
                    margin:10px 0px 0px 30px;
                    &-cny{
                        display: inline-block;
                        margin-top:10px;
                        vertical-align: top;
                        // font-size:20px;/*px*/
                    }
                    &-num{
                        display: inline-block;
                        margin-left:-10px;
                        font-size:60px;/*px*/
                    }
                }
                .discount-price{
                    display: inline-block;
                    .oldprice{
                        text-align: center;
                        font-size:22px;/*px*/
                        text-decoration: line-through;
                        color:@fontcolor;
                    }
                    .discount-text{
                        background: #FEE6E8;
                        color:@chosecolor;
                    }


                }

                .detail-commits{
                    margin-top:30px;
                    padding:0px 20px;
                    
                    .detail-commit-item{
                        margin-top:30px;
                        width:100%;
                        height:250px;/*px*/
                        border-bottom: @border;
                        &-user{
                            width:100%;
                            height:100px;
                            img{
                                vertical-align: middle;
                                width:100px;
                                height:100px;
                                border-radius: 50px;
                            } 
                            span{
                                font-size:30px;/*px*/
                            }
                        }
                        &-content{
                            margin-top:20px;
                            &-text{
                                font-size:30px;/*px*/
                            }
                            &-time{
                                margin-top: 10px;
                                font-size:26px;/*px*/
                            }
                        }
                    }
                }
            }
        }
        .collect{
            width: 100%;
            height:100px;/*px*/
            position: fixed;
            bottom:0px;
            text-align: center;
            input{
                width:100%;
                height:100%;
                background: @chosecolor;
                color:white;
                font-size:50px;/*px*/
               
            }
            
        }
    }
</style>



