<template>
  <div class="hc-box">
    <TopBar></TopBar>
    <div class="container"  >
      <BscrollView class="wrapper" ref="wrapper">
        <div class="content">
            <!-- 轮播 -->
          <div class="swiper-wrap">
            <swiper :options="swiperOptions" ref="mySwiper">
              <swiper-slide v-for="(v,i) of home.wraps" :key="i">
                <img :src="v.image_800" >
              </swiper-slide>
             
              <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
          </div>

          <div class="navbar" >
            <nav-item v-for="(v,i) of home.navcontents" :key="i" :value='v'></nav-item>
          </div>

          <div class="nav-content">
            超实惠-促销直达
          </div>

          <promotion-nav :promotions="home.promotions" ></promotion-nav>

          <div class="nav-content">
            热门分类
          </div>

           <div class="hotclassify" >
            <hot-classify v-for="(v,i) of home.hotclasses" :key="i" :value="v"></hot-classify>
          </div>

           <div class="nav-content">
            猜你喜欢
          </div>

          <div class="likes">
            <ProductItem v-for="(v,i) of home.likes" :key="i" :value="v"  > 
              <template slot="title">
                <span class="product-item-content-desc">
                <span class="product-item-content-pre">热卖</span>
                {{v.title}}
              </span>
              </template>
              <template slot="price">
                 <span class="product-item-foot-price">￥{{v.price}}</span>
              </template>
              
            </ProductItem>
          </div>


        </div>
      </BscrollView>
    </div>
    <FootBar></FootBar>
  </div>
  
</template>

<script>
import BScroll                    from 'better-scroll';
import { swiper, swiperSlide }    from 'vue-awesome-swiper';
import NavItem                    from './Container/NavItem';
import PromotionNav               from './Container/PromotionNav';
import HotClassify                from './Container/HotClassify';


import {mapActions,mapGetters,mapMutations,mapState} from 'vuex';


 
export default {
  components:{
    swiper,
    swiperSlide,
    pagination:'.swiper-pagination',
    NavItem,
    PromotionNav,
    HotClassify,
    
  },
  data(){
    return{
      swiperOptions:{
        autoplay:2000,
        loop:true,
        pagination:'.swiper-pagination',
        paginationClickable:true
      },
     
    }
  },
  computed:{
    ...mapState(['home']),
  },
  methods:{
    ...mapActions(["loadHomeData"]),
    ...mapActions(["loadLikes"])
  },
  mounted(){

    this.loadHomeData();
    this.loadLikes();
  }  
}
</script>

<style lang="less" scoped>

  .hc-box{
   
    position: absolute;
    top:0px;/*px*/
    bottom:100px;/*px*/
    left: 0;
    right: 0;
  }
  .container{
    
    height: 100%;
    .wrapper{
      width:100%;
      height:100%;
      .content{   
        .swiper-wrap{
            width:100%;
            height:400px;/*px*/
            img{
              width:100%; 
              height:400px;/*px*/
            }
            .swiper-pagination{
              background-color: rgba(51, 51, 51, 0.4);
            }
        }
        .navbar{
          width:100%;
          height: 350px;/*px*/
          padding:20px 0;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
        }
        .hotclassify{
          width:100%;
          height: 800px;/*px*/
          padding:20px 0;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
        }
        .likes{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
          margin-top:20px;
        }
      }
    }
  }
 
  .nav-content{
    width:100%;
    height:40px;/*px*/
    line-height: 40px;/*px*/
    margin-top:20px;/*px*/
    text-align: center;
    font-size:28px;/*px*/
    background: url(https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png) no-repeat ;
    background-size: 70%;/*px*/
    background-position: center center;
  }
  .product-item-content-desc{
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  } 

  .product-item-foot-price{
    font-size:30px;/*px*/
    font-weight: bold;
    margin-left:10px;
  }
</style>



