import filters          from './filters';
import ProductItem      from './components/ProductItem';
import BscrollView      from './components/BscrollView';

import TopBar           from './components/TopBar-FootBar/TopBar';
import FootBar          from './components/TopBar-FootBar/FootBar';
import TopBack          from  './components/TopBar-FootBar/TopBack';

let plugins = {};

plugins.install = function(Vue,options){
    Vue.filter("replaceBrack",filters.replaceBrack);
    Vue.component("ProductItem",ProductItem);
    Vue.component("BscrollView",BscrollView);
    Vue.component("TopBar",TopBar);
    Vue.component("FootBar",FootBar);
    Vue.component("TopBack",TopBack);





}

export default plugins;


