<template>
    <div class="product-item" @click="goToDetail()">
        <!-- 商品的图片 -->
        <div class="product-item-img">
            <img :src="value.img" alt="">
        </div>
         <!-- 商品的内容 -->
        <div class="product-item-content">
            <!-- 商品的名称 -->
            <slot name="title"></slot>
        </div>
        <div class="product-item-foot">
            <!-- 商品价格 -->
            <span class="product-item-foot-price">
                <slot name="price"></slot>
                </span>
            <div class="product-item-foot-commit">{{value.cfav}}</div>
            
        </div>
    </div>
</template>

<script>
export default {
    props:{
        value:null
    },
    methods:{
        goToDetail(){
            sessionStorage.setItem("item",JSON.stringify(this.value));
            this.$router.push("/detail");
        }
    }
}
</script>

<style lang="less" scoped>
    .product-item{
        width:46%;
        height:620px;/*px*/
        &-img{
            width:100%;
            height:460px;/*px*/
            img{
                width:100%;
                height:100%
            }
        }
        &-content{
            width:93%;
            height:80px;/*px*/
            margin:20px auto 0px;
            &-pre{
                background:#F8E3AB;
                font-size:26px;/*px*/
            }
            
        }
        &-foot{
            width:100%;
            &-price{
                // font-size:30px;/*px*/
                // font-weight: bold;
                // margin-left:10px;
            }

            
            &-commit{
                font-size:28px;/*px*/
                text-align: right;
                padding-right:40px;
                float:right;
                // margin-right:10px;
                background: url(data:image/png;base64,) center right no-repeat ;
                background-size:30px 30px;
                // background-position: center right;
            }
        }



    }
</style>



