<template>
    <div class="as-box">
        <TopBack :title="temp"></TopBack>
        <div class="container">
             <BscrollView class="wrapper" ref="wrapper">
                 <div class="content">
                    <div class="item-list">
                        <ul>
                            <li v-for="(v,i) of home.allNav" :key="i" :class="{checked:checkedId==v.sortKey}" @click="typeDownItemChose(v.sortKey)">{{v.title}}</li>
                        </ul>
                    </div>
                    <div class="product-item">
                        <ProductItem v-for="(v,i) of home.allProductDatas" :key="i" :value="v">   
                            <template slot="title">
                                <div class="item-label">
                                    <div v-for="(prop,i) of v.props" :key="i" >{{prop}}</div>
                                </div>
                            </template>


                            <template slot="price">
                                <span class="product-item-foot-price">￥{{v.price}}</span>
                            </template>
                        </ProductItem>
                        <!-- {{home.allProductDatas.wall.docs}} -->
                    </div>
                   
                    
                 </div>
             </BscrollView>
        </div>
    </div>
</template>

<script>
import {mapActions,mapGetters,mapMutations,mapState} from 'vuex';


export default {
    data(){
        return{
            checkedId:"pop",
            temp:"",
            id:null,
            
        }
    },

    computed:{
        ...mapState(['home']),
    },
    methods:{
        ...mapActions(["loadAllProductDatas"]),
        ...mapActions(["loadTypeRightDownDataItems"]),
        ...mapActions(["loadAllProductDatas2"]),

        typeDownItemChose(sort){
            this.checkedId = sort;
            console.log(this.temp);
            this.temp = this.$route.params.temp;
            this.id = this.$route.params.id;

            if(!this.id){
                this.loadAllProductDatas({q:this.temp,sort:sort});
                return;
            }

            this.loadAllProductDatas2({fcid:this.id ,sort:sort ,title:this.temp})
            
        },
    },

    mounted(){
        this.temp = this.$route.params.temp;
        // console.log(this.temp);
        this.id = this.$route.params.id;
        
        console.log(this.temp);
        if(!this.id){
            console.log(this.temp);
            this.loadAllProductDatas({q:this.temp,undefined});
            return;
        }
        this.loadAllProductDatas2({fcid:this.id ,undefined ,title:this.temp})
    },

    beforeRouteUpdate(to,from,next){
        console.log(to);
        console.log(from);
        this.loadAllProductDatas({q:to.params.temp,undefined});
        next();
    }
    
    
}
</script>

<style lang="less" scoped>
    @import url("../../global/less");

    .as-box{
        position: absolute;
        top:0px;/*px*/
        bottom:0px;/*px*/
        left: 0;
        right: 0;
        .container{
            height: 100%;
            .wrapper{
                width:100%;
                height:100%;
                .content{
                    .item-list{
                        width:100%;
                        height:80px;
                        border-bottom:@border;
                        ul{
                            width:100%;
                            height:54px;
                            line-height: 54px;
                            margin-top:13px;
                            display: flex;
                            li{
                                width:33.33333%;
                                text-align: center;
                                font-size:30px;/*px*/
                                border-left:@border;
                            }
                        }
                    }
                    .product-item{
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-around;
                        .product-item-foot-price{
                            font-size:30px;/*px*/
                            font-weight: bold;
                            margin-left:10px;
                        }
                    }
                }
            }
        }  
    }
    .item-label{
        >div{
            display: inline-block;
            border:@border;
            border-radius: 5px;
            margin-left:10px;
            font-size:22px;/*px*/
            background: @tagback;

        }
    }
    .checked{
        color:@chosecolor;
    }
</style>



