<template>
    <div class="login">
        <div class="username">
            用户名: <input type="text" v-model="user.uname">
        </div>
        <div class="password" >
            密&nbsp;&nbsp;&nbsp;码: <input type="password" v-model="user.upass">
        </div>
        <div class="login-btn">
            <button :disabled="isEmpty" @click="login()">登&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;录</button>
        </div> 
    </div>
</template>

<script>
export default {
    data(){
        return{
            user:{
                uname:"",
                upass:"",
                isLogin:true
            }
        }
    },
    computed:{
        isEmpty(){
            return this.user.uname==""&&this.user.upass=="";
        }
    },
    methods:{

        login(){
            this.$router.go(-1);
            sessionStorage.setItem("users",JSON.stringify(this.user));
        }
    }
}
</script>

<style lang="less" scoped>
    @import url("../../global/less");

    .login{
        width:100%;
        height:100%;
        .username{
            vertical-align: top;
            margin-top:200px;
            font-size:40px;/*px*/
            text-align: center;
            input{
                width:400px;
                height:50px;
                font-size:40px;
                border:@border;
            }
        }
        .password{
            vertical-align: top;
            margin-top:30px;
            font-size:40px;/*px*/
            text-align: center;
            input{
                width:400px;
                height:50px;
                font-size:40px;
                border:@border;
            }
        }
        &-btn{
            width:80%;
            height:100px;
            margin:60px auto;
            button{
                width:100%;
                height:100%;
                background:@chosecolor; 
                text-align: center;
                font-size:40px;/*px*/
                color:white;
            }
        }

    }
</style>



