<template>
    <div class="topbar">
        <div class="topbar-init" v-show="flag">
            <div class="topbar-init-left">
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </div>
            <div class="topbar-init-search" @click="changeSearch()">
                <div>
                    <i class="iconfont icon-sousuo"></i>
                    <span>连衣裙</span>
                </div>
            </div>
            <div class="topbar-init-right">
                <i class="iconfont icon-gouwuche"></i> 
            </div>
        </div>

        <div class="topbar-search" v-show="!flag">
            <div class="topbar-search-left" @click="backHome()">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="topbar-search-input">
                <input type="text" placeholder="连衣裙" v-model="temp">
            </div>
            <div class="topbar-search-right" @click="goToAllSearch(temp)">
                <span>搜索</span>
            </div>
        </div>
        
        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div class="topbar-content" v-show="!flag">
                <div class="topbar-content-history">
                    <div class="topbar-content-history-left">
                        <i class="iconfont icon-sousuo"></i>
                        <span>历史搜索</span>
                    </div>
                    <div class="topbar-content-history-right">
                        <i class="iconfont icon-lajixiang"></i>
                    </div>
                    <div class="topbar-content-history-temp">
                        暂无历史搜索
                    </div>
                </div>
                <div class="topbar-content-hot">
                    <div class="topbar-content-hot-title" >
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                    </div>
                    <div class="topbar-content-hot-temp">
                        <div v-for="(v,i) of home.keyWord" :key="i" :style="{'color':v.color}" @click="goToAllSearch(v.frontword)" >{{v.frontword}}</div>
                    </div>
                </div>

            </div>
        </transition>
        

    </div>
</template>

<script>
import {mapActions,mapGetters,mapMutations,mapState} from 'vuex';

export default {
    
    data(){
        return{
            flag:true,
            temp:null
        }
    },
    computed:{
        ...mapState(['home']),
    },
 
    methods:{
        ...mapActions(["loadKeyWordData"]),
        
        goToAllSearch(data){
           
            this.$router.push("/allsearch/"+data);
            this.backHome();
        },

        changeSearch(){
           this.flag=false;
           
        },
        backHome(){
            this.flag=true;
        }
    },
    mounted(){
        this.loadKeyWordData();
    }
};
</script>

<style lang="less" scoped>
    @import url("../../../global/less");

    .topbar {
        width: 100%;
        line-height: 100px; /*px*/
        border-bottom: @border;
        &-init{
            display: flex;
            justify-content: space-between;
            .iconfont {
                font-size: 50px; /*px*/
            }
            &-left {
                margin-left: 30px; /*px*/
            }
            &-search {
                background: #eee;
                border-radius: 10px;
                margin: 20px 0px;
                width: 70%;
                > div {
                color: @fontcolor;
                height: 100%;
                line-height: 60px; /*px*/
                .iconfont {
                    font-size: 30px; /*px*/
                    margin-left: 10px; /*px*/
                }
                }
            }
            &-right {
                margin-right: 30px; /*px*/
            }
        }

        &-search{
            width:100%;
            display: flex;
            justify-content: space-around;
            &-left{
                margin-left:10px;
                i{
                    font-size:50px;/*px*/
                }
            }
            &-input{
                margin-left:10px;
                width:70%;
                height:100%;
                input{
                    width:96%;
                    height:50px;
                    font-size:28px;/*px*/
                    border:1px solid red;
                }
            
            }
            &-right{
                font-size:30px;/*px*/
                margin-right:12px;
            }
        }

        &-content{
            position: absolute;
            z-index: 999;
            top:112px;/*px*/
            bottom: 0px;/*px*/
            left: 0px;
            right: 0px;
            background: white;
            &-history{
                height:120px;
                line-height: 50px;
                padding-top:30px;
                &-left{
                    width:70%;
                    height:50px;
                    line-height: 50px;
                    display: inline-block;
                    margin-left:30px;
                    font-size:28px;/*px*/
                    color:@fontcolor;
                    i{
                        font-size:36px;/*px*/
                    }
                }
                &-right{
                    height:50px;
                    line-height: 50px;
                    float: right;
                    margin-right:36px;
                    i{
                        font-size:30px;/*px*/
                        color:@fontcolor;
                    }
                }
                &-temp{
                    display: inline-block;
                    height:50px;
                    line-height: 50px;
                    font-size:28px;/*px*/
                    margin-left:74px;
                    color:@fontcolor;
                }

            }

            &-hot{
                padding-top:30px;
                margin-top:30px;
                border-top:@border;
                line-height:40px;
                &-title{
                    margin-left:30px;
                    font-size:30px;
                    color:@fontcolor;
                }
                &-temp{
                    margin:30px auto 0px;
                    width:80%;
                    height:200px;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-around;
                    >div{
                        border:@border;
                        width:100px;
                        height:40px;
                        text-align: center;
                    }
                }

            }
        }
    }
</style>





