import Vue                    from 'vue';
import Router                 from 'vue-router';


import HomeComponent          from '../components/HomeComponent';
import ClassifyComponent      from '../components/Container/ClassifyComponent';
import DetailComponent        from '../components/Container/DetailComponent';
import AllSearch              from '../components/Container/AllSearch';
import AboutMe                from '../components/Container/AboutMe';
import ShopCart               from '../components/Container/ShopCart';  
import LoginComponent         from '../components/Container/LoginComponent';


Vue.use(Router);

const router = new Router({
    mode:"history",
    linkActiveClass:"active",
    linkExactActiveClass:"active-exact",
    routes: [
        {path:'/',redirect:'home'},
        {path:'/home',component:HomeComponent},
        {path:'/detail',component:DetailComponent},
        {path:"/types",component:ClassifyComponent},
        {path:"/allsearch/:id?/:temp",component:AllSearch},
        {path:"/aboutme",component:AboutMe},
        {path:"/shopcart",component:ShopCart},
        {path:"/login",component:LoginComponent}


    ]
})

const isLginPage = ["/detail"];

router.beforeEach((to,from,next)=>{
    next();
})
router.beforeEach((to,from,next)=>{   
    let users = JSON.parse(sessionStorage.getItem("users")) ;
    // console.log(users);
    if(users==null){
        users={};
    };
    let isLogin = users.isLogin;
    // console.log(isLogin);
    // console.log(to);
    // console.log(from);
    if(isLginPage.indexOf(to.path)!=-1&&!isLogin){
        next("/login");
    }
    next();
})

export default router;




