<template>
    <div class="promotion-nav">
        <div class="promotion-up">
            <div class="promotion-up-left">
               <div class="promotion-up-left-title promotion-title">{{promotions[0]?promotions[0].title:''}} </div>
               <div class="promotion-vicetitle">{{promotions[0]?promotions[0].viceTitle:'' | replaceBrack}} </div>
               <div class="promotion-up-left-img">
                   <img :src="promotions[0]?promotions[0].image:''">
               </div>
            </div>
            <div class="promotion-up-right">
                <div class="promotion-up-right-item">
                    <div class="promotion-up-right-item-left">
                        <span class="promotion-title">{{promotions[1]?promotions[1].title:''}}</span><br>
                        <span class="promotion-vicetitle">{{promotions[1]?promotions[1].viceTitle:'' | replaceBrack}}</span>
                    </div>
                    <div class="promotion-up-right-item-img">
                        <img :src="promotions[1]?promotions[1].image:''">
                    </div> 
                </div>

                <div class="promotion-up-right-item">
                    <div class="promotion-up-right-item-left">
                        <span class="promotion-title">{{promotions[2]?promotions[2].title:''}}</span><br>
                        <span class="promotion-vicetitle">{{promotions[2]?promotions[2].viceTitle:'' | replaceBrack}}</span>
                    </div>
                    <div class="promotion-up-right-item-img">
                        <img :src="promotions[2]?promotions[2].image:''">
                    </div> 
                </div>
            </div>
        </div>

        <div class="promotion-down">
            <div class="promotion-down-item">
                <div class="promotion-down-item-title promotion-title">{{promotions[3]?promotions[3].title:''}} </div>
                <div class=" promotion-vicetitle">{{promotions[3]?promotions[3].viceTitle:'' | replaceBrack}}</div>
                <div class="promotion-down-item-img">
                    <img :src="promotions[3]?promotions[3].image:''">
                </div>
            </div>

            <div class="promotion-down-item">
                <div class="promotion-down-item-title promotion-title">{{promotions[4]?promotions[4].title:''}} </div>
                <div class=" promotion-vicetitle">{{promotions[4]?promotions[4].viceTitle:'' | replaceBrack}}</div>
                <div class="promotion-down-item-img">
                    <img :src="promotions[4]?promotions[4].image:''">
                </div>
            </div>
            <div class="promotion-down-item">
                <div class="promotion-down-item-title promotion-title">{{promotions[5]?promotions[5].title:''}} </div>
                <div class=" promotion-vicetitle">{{promotions[5]?promotions[5].viceTitle:'' | replaceBrack}}</div>
                <div class="promotion-down-item-img">
                    <img :src="promotions[5]?promotions[5].image:''">
                </div>
            </div>

           
           
        </div>
        
    </div>
</template>

<script>
    export default {
        props:{
            promotions:null
        }

    }
</script>

<style lang="less" scoped>
    @import url("../../global/less");

   .promotion-nav{
        margin-top:20px;/*px*/
        width: 100%;
        height:650px;/*px*/
        border:@border;
        .promotion-title{
            font-size:28px;/*px*/
        }
        .promotion-vicetitle{
            color:#FC5979;
        }
        .promotion-up{
            width:100%;
            height:50%;
            border-bottom:@border;
            display: flex;
            &-left{
                box-sizing: border-box;
                width:50%;
                height: 100%;
                border-right:@border;
                text-align: center;
                &-title{
                    margin-top:54px;/*px*/
                }
            }
            &-right{
                box-sizing: border-box;
                width:50%;
                height: 100%;
                &-item{
                    height:50%;
                    box-sizing: border-box;
                    border-bottom: @border;
                    &-left{
                        margin-top:42px;
                        margin-left: 16px;
                        height:100%;
                        vertical-align: middle;
                        display: inline-block; 
                    }
                    &-img{
                        float: right;
                    }
                } 
            }
       }
       .promotion-down{
            width:100%;
            height:50%;
            box-sizing: border-box;
            display: flex;
            &-item{
                width: 33.33333%;
                height:100%;
                box-sizing: border-box;
                border-right:@border;
                text-align:center;
                &-title{
                    margin-top:16px;/*px*/
                }
                &-img{
                    margin-top:20px;
                }
            }

       }
   }
</style>



