<template>
    <div class="navitem">
        <div class="navitem-img" >
            <img :src="value.image">
        </div>
        <div class="navitem-title">
           {{ value.title }}
        </div>
    </div>
</template>

<script>

    
export default {
    props:{
        value:null
    }      
}

</script>

<style lang="less" scoped>
    .navitem{
        width:150px;
         
        &-img{
            margin: 0 auto;
            width:100px;
            height:100px;/*px*/
            border-radius:50px; 
            text-align: center;
            img{
                width:100%;
                height:100%;
                text-align: center;
            }
        }
        &-title{
            margin-top:16px;
            font-size: 22px;/*px*/
            text-align: center;
        }
    }
</style>



