<template>
    <div>
        <TopBar></TopBar>
        <div class="list-left">
            <div class="container">
                <BscrollView class="wrapper" ref="wrapper">
                    <div class="content">
                       <ul>
                            <li v-for="(v,i) of home.typeleft" :key="i" :class="{selected:selectId==v.maitKey}" @click="typeChose(v.maitKey,v.miniWallkey)">
                                <div>{{ v.title }}</div>
                            </li>
                       </ul>
                    </div>
                </BscrollView>
            </div>
        </div>

        <div class="list-right">
            <div class="container">
                <BscrollView class="wrapper" ref="wrapper">
                    <div class="content">
                        <div class="list-right-upper">
                            <div v-for="(v,i) of home.typerightUpper" :key="i" class="list-right-upper-item" @click="goToAllSearch(v.link,v.title)">
                                <div class="list-right-upper-item-img">
                                    <img :src="v.image">
                                </div>
                                <div  class="list-right-upper-item-title">
                                     {{v.title}}
                                </div>
                            </div>
                        </div>
                        <div class="list-right-down">
                            <div class="list-right-down-nav">
                                <ul>
                                    <li v-for="(v,i) of home.typerightDownNav" :key="i" :class="{checked:checkedId==v.sortKey}"  @click="typeDownItemChose(v.sortKey)">
                                        {{v.title}}
                                    </li>
                                     
                                </ul>
                            </div>
                            <div class="list-right-down-items">
                                <!-- <ProductItem v-for="(v,i) of 10" :key="i"></ProductItem> -->
                                <ProductItem v-for="(v,i) of home.typerightDownItem" :key="i" :value="v">
                                     <template slot="title">
                                        <span class="product-item-content-desc">
                                            <span class="product-item-content-pre">热卖</span>
                                            {{v.title}}
                                        </span>
                                    </template>
                                    <template slot="price">
                                        <span class="product-item-foot-price">￥{{v.price}}</span>
                                    </template>
                                </ProductItem>
                            </div>
                        </div>
                    </div>
                </BscrollView>
            </div>
        </div>
        <FootBar></FootBar>
    </div>
</template>

<script>
import BScroll from 'better-scroll';

import {mapActions,mapGetters,mapMutations,mapState} from 'vuex';


export default {
    data(){
        return{
           
            selectId:41888,
            checkedId:"pop",
            initPid:41888,
            initFcid:10062603,
             
        }
    },
    methods:{
         goToAllSearch(data1,data2){
            let start = data1.indexOf("?")+1;
            let end   = data1.indexOf("&");
            data1 = data1.substring(start,end).split("=")[1];
            console.log(data1);
            this.$router.push("/allsearch/"+data1+"/"+data2);
        },
       

        typeChose(data1,data2){
            this.selectId = data1;
            this.loadTypesRightData(data1);
            this.loadTypeRightDownData(data2);
            this.loadTypeRightDownDataItems({undefined});

        },

        typeDownItemChose(data){
            console.log(data);
            this.checkedId = data;
            this.loadTypeRightDownDataItems({undefined,sort:data});
        },
       


        ...mapActions(["loadHomeData"]),
        ...mapActions(["loadTypesRightData"]),
        ...mapActions(["loadTypeRightDownData"]),
        ...mapActions(["loadTypeRightDownDataItems"]),

    },
    computed:{
        ...mapState(['home']),
    },
    mounted(){
    
        this.loadHomeData();
        this.loadTypesRightData(this.initPid);
        this.loadTypeRightDownData(this.initFcid);
        this.loadTypeRightDownDataItems({undefined});

    }
}
</script>

<style lang="less" scoped>
    @import url("../../global/less");

    .list-left{
        position: absolute;
        top:112px;/*px*/
        bottom:100px;/*px*/
        width:25%;
        .container{
            height:100%;
            .wrapper{
                width:100%;
                height:100%;
                .content{
                    ul{
                          background: @typeback;
                        li{
                            width:100%;
                            height:100px;/*px*/
                            line-height: 100px;/*px*/
                           
                          
                            >div{
                                height:60px;/*px*/
                                line-height: 60px;/*px*/
                                text-align: center;
                                margin-top:20px;/*px*/
                            }
                        }
                    }
                }
            }
        }
    }
    .list-right{
        position: absolute;
        top:112px;/*px*/
        bottom:100px;/*px*/
        left:25%;
        width:75%;
        float:right;
        .container{
            height:100%;
            .wrapper{
                width:100%;
                height:100%;
                .content{
                    .list-right-upper{
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-around;
                        &-item{
                            margin-top:30px;
                            width:28%;
                            height:200px;/*px*/
                            text-align: center;
                            &-img{
                                width:100%;
                                height:80%;
                                img{
                                    width:100%;
                                    height:100%;
                                }
                            }
                            &-title{
                                margin-top:10px;
                            }
                        }
                    }

                    .list-right-down{
                        margin-top:60px;
                        &-nav{
                            width:100%;
                            height:80px;
                            border-top:@border;
                            border-bottom: @border;
                            
                            ul{
                                margin-top:10px;
                                height:60px;
                                line-height: 60px;
                                display: flex;
                                li{
                                    vertical-align: middle;
                                    text-align: center;
                                    width:33.33333%;
                                    font-size: 30px;/*px*/
                                    border-left:@border;
                                }
                            }
                            
                        }
                        &-items{
                            display: flex;
                            flex-wrap: wrap;
                            justify-content: space-around;
                        }
                    }
                }
            }
        }
      
    }  
    .product-item-content-desc{
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
    } 
    .product-item-foot-price{
        font-size:30px;/*px*/
        font-weight: bold;
        margin-left:10px;
        color:@chosecolor;
    }
    .selected{
        background: white;
        >div{
            color:@chosecolor;
            border-left:5px solid @chosecolor;
        }
    }
    .checked{
        color:@chosecolor;
    }
</style>



