import jsonpUtil from '../util/jsonpUtil';

const mce               = "https://mce.mogucdn.com/jsonp/";

const like              = "https://list.mogujie.com/search";

const typeRightUpper    = "https://mce.mogujie.com/jsonp/";

const typeRightDown     = "https://list.mogujie.com/search";

const detailCommit      = "https://rate.mogujie.com/h5/rate/jsonp/";

const keyWordSearch     = "https://list.mogujie.com/module/mget";

const allProducts       = "https://list.mogujie.com/search";


export default {
    /**
     * 根据参数pids 查询相关数据
     * @param pids:string 
     */ 
    loadDataByPids(params){
        let url= mce + "multiget/3";
        return jsonpUtil.request(url,params);
    },

    // 猜你喜欢数据查询
    loadLikesData(params){
        let url = like;
        return jsonpUtil.request(url,params);
    },


    //分类页面右侧上部数据查询
    loadTypeRightDataByPid(params){
        let url = typeRightUpper +"makeup/3";
        return jsonpUtil.request(url,params);
    },

    //分类页面右侧下面数据查询
    //通过左侧整体数据查询
    loadTypeRightDataByFcid(params){
        let url = typeRightDown;
        return jsonpUtil.request(url,params);
    },
    //通过左侧跟导航数据查询
    loadTypeRightDownData(params){
        let url = typeRightDown;
        return jsonpUtil.request(url,params);
    },

    //详情页面评论数据加载
    loadDetailCommit(params){
        let url = detailCommit +"x6.rate.ratelist/v1";
        return jsonpUtil.request(url,params);
    },


    //关键字搜索数据加载
    loadKeyWordSearch(params){
        let url = keyWordSearch;
        return jsonpUtil.request(url,params);
    },

   

    //全网搜索数据
    loadAllProducts(params){
        let url = allProducts;
        return jsonpUtil.request(url,params);
        
    }

}


