<template>
    <div class="hcitem">
        <div class="hcitem-img" >
            <img :src="value.image">
        </div>
        <div class="hcitem-title">
            {{value.title}}
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            value:null
        }
    }
</script>

<style lang="less" scoped>
    .hcitem{
        margin-top:20px;
        width:160px;
        &-img{
            margin: 0 auto;
            width:100px;
            height:100px;/*px*/
            border-radius:50px; 
            text-align: center;
            img{
                width:100%;
                height:100%;
                text-align: center;
            }
        }
        &-title{
            margin-top: 16px;
            font-size: 24px;/*px*/
            text-align: center;
        }
    }
</style>



