<template>
    <div>
        <!-- 购物车头部 -->
        <div class="topper">
            <div class="topper-left" @click="$router.go(-1)">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="topper-middle">
                购物车({{keys.length}})
            </div>
            <div class="topper-right">
                <span>编辑</span>
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </div>
        </div>
        <!-- 收藏的商品展示部分 -->
        <div class="sc-box">  
            <div class="container">
                <BscrollView class="wrapper" ref="wrapper">
                    <div class="content">
                        <div class="collected" v-for="(v,i) of products" :key="i">
                            <div class="collected-checked">
                                <input type="checkbox" v-model="v.status" @change="checkAll()">
                            </div>
                            <div class="collected-item">
                                <div class="collected-item-img">
                                    <img :src="v.img">
                                </div>
                                <div class="collected-item-content">
                                    <div class="collected-item-content-desc">
                                       {{ v.title }}
                                    </div>
                                    <div class="collected-item-content-intro">
                                        <!-- 规格：去屑止痒； -->
                                    </div>
                                    <div class="collected-item-content-price">
                                        <span class="collected-item-content-price-new">￥{{v.price}}</span>
                                        <span class="collected-item-content-price-old">￥{{v.orgPrice}}</span>
                                        <span class="collected-item-content-price-num">x{{v.num}}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                        <!-- <div v-for="(v,i) of 65" :key="i">aaaa</div> -->
                    </div>
                </BscrollView>
            </div> 
        </div>
        <!-- 底部全选以及付款价格 -->
        <div class="footer">
            <div class="footer-checked">
                <input type="checkbox" v-model="all" @change="selectAll()" :disabled="isEmpty">
            </div>
            <div class="footer-text">
                <span class="footer-text-num">全选({{checkNum}})</span>
                <span class="footer-text-price">￥{{totalPrice}}</span>
            </div>
            <div class="footer-btn">
                <button :disabled="isEmpty">去结算</button>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    data(){
        return{
            all:false,
            products:{},
            keys:[],
            checkNum:0,
            totalPrice:0.00
        }
    },
    computed:{
        isEmpty(){
            return this.products.length==0;
        }
    },
    methods:{
        checkAll(){
            this.all = true;
            this.checkNum = this.keys.length;
            this.totalPrice = 0;
            for(let key in this.products){
                if(!this.products[key].status){
                    this.all = false;
                    this.checkNum--;
                }else{
                    this.totalPrice += this.products[key].price*this.products[key].num;
                }
            }
            console.log(this.totalPrice);
        },

        selectAll(){
            
            for(let key in this.products){
                
                this.products[key].status = this.all;
                if(this.all){
                    this.totalPrice += this.products[key].price*this.products[key].num;
                    this.checkNum = this.keys.length;
                }else{
                    this.totalPrice = 0;
                    this.checkNum = 0;
                }
            }
        },

        loadProductsLength(){
            for(let key in this.products){
               this.keys.push(key);
            }
            return this.keys;
        }
    },


    mounted() {
        let user = JSON.parse(sessionStorage.getItem("users"));
        // console.log(user);
        if(user){
            this.products=JSON.parse(localStorage.getItem(user.uname)) ;  
        }
        this.loadProductsLength();
    },
}
</script>

<style lang="less" scoped>
    @import url("../../global/less");

    .topper{
        width:100%;
        height:100px;/*px*/
        display:flex;
        justify-content: space-between;
        align-items: center;
        &-left{
            margin-left:30px;
            text-align: center;
            i{
                font-size:40px;/*px*/
            }
        }
        &-middle{
            text-align: center;
            font-size: 40px;/*px*/
            margin-left:10px;
        }
        &-right{
            margin-right:20px;
            font-size:30px;/*px*/
            text-align: center;
        }

    }
    .sc-box{
        position: absolute;
        top:100px;/*px*/
        bottom:100px;/*px*/
        left: 0;
        right: 0;
        .container{
            height: 100%;
            .wrapper{
                width:100%;
                height:100%;
                background: #eee;
                .content{
                    .collected{
                        width:100%;
                        height:300px;/*px*/
                        background: white;
                        margin-top:30px;
                        display: flex;
                        align-items:center;
                        padding-left:20px;
                        &-checked{
                            input{
                                width:40px;
                                height:40px;
                            } 
                        }
                        &-item{
                            margin-left:40px;
                            width:87%;
                            height:80%;
                            display:flex;
                            align-items: top;
                            &-img{
                                width:150px;
                                height:100%;
                                img{
                                    width:100%;
                                    height:100%;
                                }
                            }
                            &-content{
                                width:70%;
                                margin-left:20px;
                                &-desc{
                                    font-size:26px;/*px*/
                                    line-height: 40px;
                                }
                                &-intro{
                                    font-size:24px;/*px*/
                                    margin-top:10px;
                                    color: @fontcolor;
                                }
                                &-price{
                                    margin-top:50px;
                                    &-new{
                                        font-size:36px;/*px*/
                                    }
                                    &-old{
                                        color:@fontcolor;
                                        text-decoration: line-through;
                                    }
                                    &-num{
                                        float: right;
                                        line-height: 50px;
                                    }
                                }
                            } 
                        }
                    }
                }
            }
        }
    }
    .footer{
        position: absolute;
        width:100%;
        height:100px;
        bottom: 0px;
        display: flex;
        align-items: center;
        &-checked{
            margin-left: 20px;
            input{
                width:34px;
                height:34px;
            } 
        }
        &-text{
            margin-left:30px;
            &-num{
                font-size:32px;/*px*/
            }
            &-price{
                margin-left:30px;
                font-size:32px;/*px*/
                color:@chosecolor;
            }
        }
        &-btn{
            position: absolute;
            right:20px;
            button{
                width:160px;
                height:70px;
                border-radius: 10px;
                font-size:30px;/*px*/
                background: @chosecolor;
                color:white;
                
            }
        }

    }
</style>



